// let server_url = '';  //
let token = '';//token
// process.env.NODE_ENV === 'development' ? 'http://192.168**:6002' : 'http://***/api'; //

import baseUrl from './config'

//
export function service(options = {}) {
	uni.getStorageSync('token') && (token = uni.getStorageSync('token'));//token
	// options.url = options.url ? `${baseUrl}${options.url}` : baseUrl;//
	options.url = options.url ? `${baseUrl}${options.url}` : 'https://sweb.jjdzmall.com';//
	// options.url = `https://sweb-sit.jjdzmall.com${options.url}`;//
	options.method = options.method || 'POST'
	//
	options.header = {
		'content-type': 'application/json',//
		'x-token': `${token}` //Bearer 
	};
  options.timeout = 15000;
	
	if (!options.noShowLoading) {
		uni.showLoading({
				title: "",
				mask: true,
			});
	}
// promise
	return new Promise((resolved, rejected) => {
		//
		options.success = (res) => {
      uni.hideLoading();
      // if (res.data.code === 1003) {
      if ([1002,1003].includes(res.data.code)) {
        setTimeout(() => {
          uni.reLaunch({
            url: "/pages/index/index",
          });
        }, 1000);
        return uni.showToast({
          title: res.data.msg,
          icon: 'none',
          duration: 2000
        })
      }
      if (res.data.code != 0) return uni.showToast({
        title: res.data.msg,
        icon: 'none',
        duration: 2000
      })
			// if (Number(res.data.code) == 0) { //
      resolved(res.data);//
			// } else {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		duration: 3000,
			// 		title: `${res.data.msg}`
			// 	});
			// 	rejected(res.data); //
			// }

		}
		//
		options.fail = (err) => {
			uni.hideLoading();
			uni.showToast({
				// title: err.data.msg,
				title: '',
				icon: 'none',
				duration: 2000
			})
			rejected(err); //
		}
		uni.request(options);//

	});
}